*{
    padding: 0%;
    margin: 0%;
}
.login{
    background-image: url(../../img/fondo-registro.jpg);
    background-position: left !important;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    height: 100vh;
   
}
.button-modal{
    background: transparent;
    border: none;
   
    color: white;
    font-size: 20px;
    cursor: pointer;
}
.conteiner-form{
    padding-left: 2.5rem;;
    padding-top: 3rem;
}
.conteiner-form form input {
    background: rgba(235, 232, 232, 0.6);
    margin-top:1rem;
    font-size: 16px;
  }
  input[type="button"]{
      background:#1E8DF4 !important;
      color: white;
  }
  .conteiner-form p{
    padding-right: 6rem;
    padding-top: 2rem;
  }
  .conteiner-button{
    padding-top: 2.5rem;
}
.social-logo{
    padding-right: 2rem;
    width: 50px;
    height: 50px;

}
.logos{
    flex-direction: row;
    padding-right: 2rem;
    display: block;    
}

@media (min-width: 767px)  {

.conteiner-form{
    justify-content: center;
    flex-direction: column;
    align-items: center;
    padding-left: 15%;
    padding-top: 3rem;
    display: flex;
    height: 70vh;
    width: 30%;
}
.conteiner-form form{
    flex-direction: column;
    display: flex;
}
.conteiner-form form input {
    margin-top:1rem;
    font-size: 16px;
}
input[type="button"]{
    background:#1E8DF4 !important;
    color: white;
}
.conteiner-form p{
    padding-bottom: 1rem;
    padding-right: 6rem;
    padding-top: 1rem;
}
/* modal */
.button-modal{
    background: transparent;
    font-size: 20px;
    cursor: pointer;
    color: white;
    border: none;
}
.conteiner-button{
    padding-top: 2.5rem;
    display: flex;
    width: 50%;
}
/* logo  movil*/
.social-logo{
    padding-right: 4rem;
    width: 50px;
    height: 50px;
}
.logos{
    flex-direction: row;
    padding-right: 2rem;
    display: block;    
}
}